define('dummy/models/foo', ['exports', 'sl-ember-store/model'], function (exports, Model) {

    'use strict';

    var Foo = Model['default'].extend({});

    Foo.reopenClass({
        url: "/foo",
        serializer: function (data, store) {
            if (data.meta) {
                store.metaForType("foo", data.meta);
            }

            return data.foo;
        }
    });

    exports['default'] = Foo;

});